import { createRouter, createWebHistory } from 'vue-router'

//layouts
import FrontendLayouts from '@/layouts/FrontendLayout.vue'

// frontend 
import Home from '@/views/frontend/Home.vue'
import About from '@/views/frontend/About.vue'
import Service from '@/views/frontend/Service.vue'
import MyPortfolio from '@/views/frontend/MyPortfolio.vue'
import Contact from '@/views/frontend/Contact.vue'
import LoginPage from '@/views/frontend/LoginPage.vue'
import Register from '@/views/frontend/Register.vue'
import ForgotPassword from '@/views/frontend/ForgotPassword.vue'

// backend

// 404 not found
import Notfound from '@/views/frontend/Notfound.vue'

const routes = [
  {
    path: '/',
    name: 'FrontendLayouts',
    component: FrontendLayouts,
    children: [
      {
        path: '',
        component: Home,
        meta: {
          title: '',
          description: 'Home'
        }
      },
      {
        path: 'about',
        component: About,
        meta: {
          title: 'About | ',
          description: 'About'
        }
      },
      {
        path: 'service',
        component: Service,
        meta: {
          title: 'Service | ',
          description: 'Service'
        }
      },
      {
        path: 'myportfolio',
        component: MyPortfolio,
        meta: {
          title: 'My Portfolio | ',
          description: 'MyPortfolio'
        }
      },
      {
        path: 'contact',
        component: Contact,
        meta: {
          title: 'Contact | ',
          description: 'Contact'
        }
      },
      {
        path: 'register',
        component: Register,
        meta: {
          title: 'Register | ',
          description: 'Register'
        }
      },
      {
        path: 'forgotPassword',
        component: ForgotPassword,
        meta: {
          title: 'ForgotPassword | ',
          description: 'ForgotPassword'
        }
      },
      {
        path: 'login',
        component: LoginPage,
        meta: {
          title: 'Login | ',
          description: 'Login'
        }
      },
    ]
  },
  {
    path: '/:catchAll(.*)',
    name: 'Notfound',
    component : FrontendLayouts,
    children: [
      {
        path: '',
        component : Notfound,
        meta: {
          title: '404 Not found | ',
          description: '404 Not found'
        }
      }
    ],
  }

]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
